import React, { Component } from "react";
import Post from "./Post";
import { postsData } from "../data";
class Recommendation extends Component {
  render() {
    return (
      <div>
        {postsData.map((post, index) => (
          <Post key={index} post={post} />
        ))}
      </div>
    );
  }
}

export default Recommendation;
