import React, { Component } from 'react';
import { Link } from "react-router-dom";
import style from "./style.scss";
import classNames from 'classnames';
import { Fa } from 'mdbreact';

class Post extends Component {
  render() {
    const { post } = this.props;
    return (
      <div className={style.panel}>
        { post.imgUrl &&         
          <div 
            className={style.image}
            style={{backgroundImage: `url(${post.imgUrl})`}}>
          </div>} 
        {/**content */}
        <div className={classNames('d-flex flex-column', style.content)}>
          <div className={style.title}>{post.title}</div>
          <div className={style.detail}>{post.detail}</div>
          <Link to={`${post.postUrl}`} className={style.link}></Link>
          
          {/* tag and stat*/}
          <div className='d-flex align-items-end'>
            {/* tag */}
            <div className={style.tag}>
              { post.tags.map((tag, index)=>
                  <Link 
                    key={index}
                    to={tag.url}>
                    {tag.name}
                  </Link>)}
            </div>

            {/* stat */}
            <div className={classNames('d-flex align-items-center', style.stat)}>
              <div className='mr-2'>
                <Fa 
                  icon='fas fa-heart' 
                  className='mr-2'
                  style={{color: '#f66'}}/>
                {post.heartCount}
              </div>
              <div>
                <Fa icon='fas fa-comments' className='mr-2'/>
                {post.commentCount}
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Post;